import { Card, CardContent, Chip, Divider, Grid, Typography, makeStyles, Tooltip, Button, CardActions, Avatar, LinearProgress } from '@material-ui/core';
import { Update, Copyright } from '@material-ui/icons';
import React from 'react';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(0.5),
        },
    },
}));

const Projects = (props) => {
    const setTitle = props.setTitle
    const classes = useStyles()
    const [githubData, setGithubData] = React.useState([])
    const [filters, setFilters] = React.useState([])
    const [tagData, setTagData] = React.useState([])
    const [repositories, setRepositories] = React.useState([])
    React.useEffect(() => {
        setTitle('Projects')
        try {
            fetch('https://api.github.com/users/azharizkita/repos').then((response) =>
                response.json()
            ).then((result) => {
                var tagsData = {}
                var processed = {}
                result.forEach((value) => {
                    if (value.name !== 'azharizkita.github.io') {
                        var tags = undefined
                        if (value.description) {
                            var metaDescription = value.description.split(']')
                            value['description'] = metaDescription[1]
                            tags = metaDescription[0].split('[')
                            if (tags.length > 1) {
                                value['tags'] = tags[1].split(',').map((val) => val.trim())
                                value['tags'].forEach((tag) => {
                                    if (typeof tagsData[tag] !== 'undefined') {
                                        tagsData[tag] += 1
                                    } else {
                                        tagsData[tag] = 1
                                    }
                                })
                            }
                        }
                        processed[+new Date(value.pushed_at)] = value
                    }
                })
                var sorted = []
                Object.keys(processed).sort((a, b) => b - a).forEach((value) => sorted.push(processed[value]))
                setGithubData(sorted)
                setTagData(tagsData)
            })
        } catch (error) {
            console.error(error)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    React.useEffect(() => {
        if (githubData) {
            const filteredRepos = githubData.map((repo) => {
                var isFiltered = true
                filters.forEach((filterName) => {
                    if (repo.tags) {
                        if (!repo.tags.includes(filterName)) {
                            isFiltered = false
                        }
                    } else {
                        isFiltered = false
                    }
                })
                if (isFiltered) {
                    return repo
                } else {
                    return null
                }
            }).filter((val) => val !== null)
            setRepositories(filteredRepos)
        }
    }, [filters, githubData])
    return (
        <>
            {githubData.length !== 0 ? (
                <>
                    <div className={classes.root} style={{ padding: 25 }}>
                        {tagData ? (
                            Object.keys(tagData).map((tag, index) => (
                                <Chip
                                    key={index}
                                    avatar={<Avatar>{tagData[tag]}</Avatar>}
                                    label={tag}
                                    onClick={() => {
                                        if (filters.includes(tag)) {
                                            setFilters(filters.filter(e => e !== tag))
                                        } else {
                                            setFilters([...filters, tag])
                                        }
                                    }}
                                    variant={filters.includes(tag) ? 'default' : 'outlined'}
                                />
                            ))
                        ) : ''}
                    </div>
                    <Grid
                        container
                        alignContent={'center'}
                        alignItems={'center'}
                        justify={'center'}
                    >
                        {repositories.length !== 0 ? repositories.map((value, i) => (
                            <Grid key={i} item xs={12} md={4} lg={4} style={{ padding: 15 }}>
                                <Card variant={'outlined'}>
                                    <CardContent style={{ minHeight: 220 }}>
                                        <Typography variant={'h6'} align={'center'} color={'textPrimary'}>
                                            {value.name}
                                        </Typography>
                                        <div style={{ paddingBottom: 10 }} />
                                        <Divider />
                                        <div style={{ paddingBottom: 10 }} />
                                        <Grid
                                            container
                                            className={classes.root}
                                            style={{ width: '100%' }}
                                        >
                                            <Grid item>
                                                <Tooltip title={'Last update'}>
                                                    <Chip
                                                        icon={<Update />}
                                                        label={new Date(value.updated_at).toLocaleDateString() + ', ' + new Date(value.updated_at).toLocaleTimeString()}
                                                        variant={'outlined'}
                                                    />
                                                </Tooltip>
                                            </Grid>
                                            <Grid item>
                                                <Tooltip title={'License'}>
                                                    <Chip
                                                        icon={<Copyright />}
                                                        label={value.license ? value.license.name : 'No License'}
                                                        variant={'outlined'}
                                                    />
                                                </Tooltip>
                                            </Grid>
                                        </Grid>
                                        <div style={{ paddingBottom: 10 }} />
                                        <Divider />
                                        <div style={{ paddingBottom: 10 }} />
                                        <Grid
                                            container
                                            className={classes.root}
                                            style={{ width: '100%' }}
                                        >
                                            {value.tags ? value.tags.map((tag, index) => (
                                                <Chip
                                                    key={index}
                                                    label={tag}
                                                    variant={'default'}
                                                />
                                            )) : ''}
                                        </Grid>
                                        {value.tags ? <div style={{ paddingBottom: 5 }} /> : ''}
                                        <Typography align={'center'}>
                                            {value.description ? value.description : 'No desription.'}
                                        </Typography>
                                    </CardContent>
                                    <CardActions>
                                        {value.tags ? (
                                            value.tags.includes('Application') ? <Button color={'primary'} variant={'contained'} onClick={() => window.open('https://azharizkita.github.io/' + value.name + '/', '_blank')} fullWidth>Launch</Button> : ''
                                        ) : ''}
                                        <Button color={'primary'} variant={'contained'} onClick={() => window.open(value.html_url, '_blank')} fullWidth>Explore</Button>
                                    </CardActions>
                                </Card>
                            </Grid>
                        )) : <Grid item style={{ paddingTop: '15%' }}>
                                <Typography variant={'h5'} color={'textSecondary'}>Seems like i haven't created that at once yet :(</Typography>
                            </Grid>}
                    </Grid>
                </>
            ) : (
                    <>
                        <Grid
                            container
                            spacing={0}
                            alignItems={'center'}
                            justify={'center'}
                            style={{ height: '80vh' }}
                        >
                            <Grid item style={{ width: 250 }}>
                                <LinearProgress variant={'buffer'} value={null} valueBuffer={null} />
                            </Grid>
                        </Grid>
                    </>
                )
            }
        </>
    )
}

export default Projects;
