import { Avatar, Grid, Typography, makeStyles, Divider, Chip, useMediaQuery, useTheme } from '@material-ui/core'
import { GitHub, MailOutline } from '@material-ui/icons/';
import Skeleton from '@material-ui/lab/Skeleton';
import React from 'react'

const useStyles = makeStyles((theme) => ({
    large: {
        width: theme.spacing(20),
        height: theme.spacing(20),
    },
    centers: {
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(0.5),
        },
    },
}));

const Welcome = (props) => {
    const theme = useTheme()
    const setTitle = props.setTitle
    const classes = useStyles()
    const isSmall = useMediaQuery(theme.breakpoints.up('sm'));
    const [bio, setBio] = React.useState({})
    React.useEffect(() => {
        setTitle('About Me')
        try {
            fetch('https://api.github.com/users/azharizkita').then((response) =>
                response.json()
            ).then((value) => {
                setBio(value)
            });
        } catch (error) {
            console.error(error)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <>
            <Grid
                container
                style={{ width: '100%', padding: 10, minHeight: isSmall ? '85vh' : '100vh' }}
                justify={'center'}
                alignContent={'center'}
                alignItems={'center'}
            >
                <Grid item xs={12} md={12} style={{ padding: 15 }}>
                    {bio.name ? (
                        <>
                            <Typography variant={'h5'} align={'center'}>Greetings!</Typography>
                            <Typography variant={'h5'} align={'center'} component={'div'}>My name is {bio.name}.</Typography>
                        </>
                    ) : (
                            <>
                                <Typography variant={'h5'} align={'center'}>Greetings!</Typography>
                                <div className={classes.centers}>
                                    <Skeleton width={250} height={24} />
                                </div>
                            </>
                        )
                    }
                </Grid>
                <Grid item xs={12} md={12} style={{ paddingBottom: 15 }}>
                    <div className={classes.centers}>
                        <Avatar alt="Remy Sharp" src={bio.avatar_url} className={classes.large} />
                    </div>
                </Grid>
                <div style={{ paddingBottom: 5 }} />
                <Grid item xs={12} md={12} style={{ paddingBottom: 15 }}>
                    <div className={classes.centers}>
                        <Divider style={{ maxWidth: 550, width: '100%', minWidth: 100 }} />
                    </div>
                </Grid>
                <Grid item xs={12} md={12} style={{ paddingBottom: 15, maxWidth: 500 }}>
                    {bio.name ? (
                        <>
                            <Typography variant={'body1'} align={'center'}>Short summary about my activity? {bio.bio}.</Typography>
                        </>
                    ) : (
                            <>
                                <div className={classes.centers}>
                                    <Skeleton width={500} variant={'text'} />
                                </div>
                            </>
                        )
                    }
                </Grid>
                <Grid item xs={12} md={12} style={{ paddingBottom: 15 }}>
                    <div className={classes.centers}>
                        <Divider style={{ maxWidth: 550, width: '100%', minWidth: 100 }} />
                    </div>
                </Grid>
                <Grid item xs={12} md={12} style={{ paddingBottom: 15 }}>
                    <Typography variant={'h6'} align={'center'}>Reach me out</Typography>
                </Grid>
                <Grid item xs={12} md={12} style={{ paddingBottom: 15 }}>
                    <div className={classes.centers}>
                        {bio.name ? (
                            <>
                                <Chip
                                    icon={<GitHub />}
                                    label={'GitHub.com/azharizkita'}
                                    onClick={() => window.open(bio.html_url, '_blank')}
                                    variant={'outlined'}
                                />
                                <Chip
                                    icon={<MailOutline />}
                                    label={'azharizkita@icloud.com'}
                                    onClick={() => window.open('mailto:azharizkita@icloud.com')}
                                    variant={'outlined'}
                                />
                            </>
                        ) : (
                                <>
                                    <Skeleton width={500} variant={'text'} />
                                </>
                            )
                        }
                    </div>
                </Grid>
            </Grid>
        </>
    )
}

export default Welcome