import { AppBar, IconButton, Toolbar, Typography, makeStyles, ListItem, ListItemIcon, ListItemText, Tooltip, SwipeableDrawer, Grid, Divider } from '@material-ui/core';
import { Menu, AccountCircle, Assessment, Brightness7, Brightness4, Book, Grain } from '@material-ui/icons/';
import { BrowserRouter as Router, Link as RouterLink } from "react-router-dom";
import { createMuiTheme, ThemeProvider } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { Helmet } from "react-helmet";
import Projects from './Projects';
import Welcome from './Welcome';
import React from 'react';
import Blogs from './Blogs';
import Covid from './Covid';


const useStyles = makeStyles((theme) => ({
    root: {
        flexGrow: 1,
    },
    menuButton: {
        marginRight: theme.spacing(2),
    },
    title: {
        flexGrow: 1,
    },
}));

const App = () => {
    const classes = useStyles()
    // eslint-disable-next-line no-unused-vars
    const [refresher, setRefresher] = React.useState()
    const [urlParameters, setParameters] = React.useState(new URLSearchParams(window.location.search))
    const section = urlParameters.get('section')
    const id = urlParameters.get('id')
    const [title, setTitle] = React.useState('')
    const [pageTitle, setPageTitle] = React.useState('')
    const [pageDescription, setPageDescription] = React.useState('')
    const [openDrawer, setOpenDrawer] = React.useState(false)
    const [isDark, setIsDark] = React.useState(window.matchMedia('(prefers-color-scheme: dark)').matches)
    const ListItemLink = (props) => {
        const { icon, primary, to } = props;
        return (
            <ListItem selected={title === primary} onClick={() => setOpenDrawer(false)} button component={React.useMemo(
                () => React.forwardRef((itemProps, ref) => <RouterLink to={to} ref={ref} {...itemProps} />),
                [to],
            )}>
                {icon ? <ListItemIcon>{icon}</ListItemIcon> : null}
                <ListItemText primary={primary} />
            </ListItem>
        )
    }
    window.matchMedia('(prefers-color-scheme: dark)').addEventListener('change', event => {
        if (event.matches) {
            setIsDark(true)
        } else {
            setIsDark(false)
        }
    })
    const theme = createMuiTheme({
        palette: {
            primary: {
                main: '#212121',
            },
            secondary: {
                main: '#424242',
            },
            type: isDark ? 'dark' : 'light'
        },
    });
    React.useEffect(() => {
        setParameters(new URLSearchParams(window.location.search))
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [window.location.search])
    React.useEffect(() => {
        if (section !== null) {
            if (section === 'blogs') {
                if (id !== null) {
                    try {
                        fetch('https://api.github.com/gists/' + id).then((response) =>
                            response.json()
                        ).then((result) => {
                            if (result && result.description) {
                                const blogMeta = result.description.split('-')
                                if (blogMeta[0].trim() === 'Blog') {
                                    const blogTitle = blogMeta[1].trim()
                                    const blogDescription = blogMeta[2].trim()
                                    setPageTitle(blogTitle[0].toUpperCase() + blogTitle.slice(1))
                                    setPageDescription(blogDescription)
                                } else {
                                    setPageTitle('Blogs')
                                }
                            } else {
                                setPageTitle('Blogs')
                            }
                        })
                    } catch (error) {
                        console.log(error)
                    }
                } else {
                    setPageTitle(section[0].toUpperCase() + section.slice(1))
                }
            } else {
                setPageTitle(section[0].toUpperCase() + section.slice(1))
            }
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [urlParameters])

    return (
        <>
            <Helmet>
                <title>{pageTitle !== '' && pageTitle !== 'Covid19' ? pageTitle : 'Covid 19 Monitor'}</title>
                <meta name="description" content={pageDescription !== '' ? pageDescription : 'Welcome to my Personal Blog!'} />
            </Helmet>
            <ThemeProvider theme={theme}>
                <CssBaseline />
                <Router>
                    <SwipeableDrawer anchor={'left'} open={openDrawer} onClose={() => setOpenDrawer(false)} onOpen={() => setOpenDrawer(true)}>
                        <div style={{ width: 'auto', height: '100vh' }} role={'presentation'}>
                            <Grid
                                container
                                style={{ width: 250, height: '100%' }}
                                justify={'center'}
                                alignContent={'center'}
                                alignItems={'center'}
                            >
                                <Typography align={'center'} color={'textPrimary'} variant={'h6'}>Section</Typography>
                                <Grid item xs={12} md={12} lg={12}>
                                    <div style={{ paddingBottom: 10 }}></div>
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <ListItemLink to={'/?section=covid19'} primary={'Covid 19 Monitor'} icon={<Grain />} />
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <ListItemLink to={'/?section=biography'} primary={'About Me'} icon={<AccountCircle />} />
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <ListItemLink to={'/?section=projects'} primary={'Projects'} icon={<Assessment />} />
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <ListItemLink to={'/?section=blogs'} primary={'Blogs'} icon={<Book />} />
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <div style={{ paddingBottom: 10 }}></div>
                                </Grid>
                                <Grid item xs={12} md={12} lg={12}>
                                    <Divider />
                                </Grid>
                                <Typography align={'center'} color={'textSecondary'} variant={'caption'}>&copy; Copyright {new Date().getFullYear()}, Azhari Rizkita</Typography>
                                <Grid item xs={12} md={12} lg={12}>
                                    <Divider />
                                </Grid>
                            </Grid>
                        </div>
                    </SwipeableDrawer>
                    <AppBar position={'static'} variant={'elevation'}>
                        <Toolbar variant={'dense'}>
                            <IconButton edge={'start'} className={classes.menuButton} onClick={() => setOpenDrawer(true)} color="inherit" aria-label="menu">
                                <Menu />
                            </IconButton>
                            <Typography variant={'h6'} className={classes.title}>
                                {title}
                            </Typography>
                            <Tooltip title={isDark ? 'Switch to light mode' : 'Switch to dark mode'}>
                                <IconButton onClick={() => setIsDark(!isDark)}>
                                    {isDark ? <Brightness4 style={{ color: 'white' }} /> : <Brightness7 style={{ color: 'white' }} />}
                                </IconButton>
                            </Tooltip>
                        </Toolbar>
                    </AppBar>
                    {section ? (
                        section === 'blogs' ? <Blogs openId={id} setPageTitle={(state) => setPageTitle(state)} setPageDescription={(state) => setPageDescription(state)} setRefresher={(state) => setRefresher(state)} setTitle={(title) => setTitle(title)} /> : (
                            section === 'projects' ? <Projects setTitle={(title) => setTitle(title)} /> : (
                                section === 'biography' ? <Welcome setTitle={(title) => setTitle(title)} /> : (
                                    section === 'covid19' ? <Covid setTitle={(title) => setTitle(title)} /> :
                                        <Covid setTitle={(title) => setTitle(title)} />
                                )
                            )
                        )
                    ) : <Covid setTitle={(title) => setTitle(title)} />}

                </Router>
            </ThemeProvider>
        </>
    )
}

export default App