import { Avatar, Grid, Typography, ListItemText, ListItemAvatar, ListItem, List, ListSubheader, DialogContent, Dialog } from '@material-ui/core'
import { Error, ErrorOutline, Favorite, FavoriteBorder, RemoveCircle, RemoveCircleOutline, RotateRight } from '@material-ui/icons';
import React from 'react';
import { Bar } from 'react-chartjs-2';

const Covid = (props) => {
    const [global, setGlobal] = React.useState({})
    const [sortedCountry, setSortedCountry] = React.useState([])
    const [lastUpdate, setLastUpdate] = React.useState('')
    const [isLandscape, setIsLandscape] = React.useState(false)
    const [countryName, setCountryName] = React.useState({})
    const setTitle = props.setTitle
    React.useEffect(() => {
        var isPotrait = window.matchMedia("(orientation: portrait)");
        if (isPotrait.matches) {
            setIsLandscape(false)
        } else {
            setIsLandscape(true)
        }

        isPotrait.addEventListener('change', (orientation) => {
            if (orientation.matches) {
                setIsLandscape(false)
            }
            else {
                setIsLandscape(true)
            }
        });
    }, [])
    React.useEffect(() => {
        setTitle('Covid 19 Monitor')
        try {
            fetch('https://api.covid19api.com/summary').then((response) =>
                response.json()
            ).then((value) => {
                const sorted = value.Countries.sort(function (a, b) {
                    return b.NewConfirmed - a.NewConfirmed;
                }).splice(0, 20);
                var finalSorted = [[], [], []]
                var newConfirmed = []
                var newRecovered = []
                var newDeaths = []
                var countryNames = {}
                Object.values(sorted).forEach((country) => {
                    newConfirmed.push(country.NewConfirmed)
                    newRecovered.push(country.NewRecovered)
                    newDeaths.push(country.NewDeaths)
                    finalSorted[0].push(country.CountryCode)
                    countryNames[country.CountryCode] = country.Country
                })
                finalSorted[1] = [{
                    data: newConfirmed,
                    label: 'New Confirmed',
                    backgroundColor: 'rgb(244, 194, 13, 0.8)'
                }, {
                    data: newRecovered,
                    label: 'New Recovered',
                    backgroundColor: 'rgba(60, 186, 84, 0.8)'
                }, {
                    data: newDeaths,
                    label: 'New Deaths',
                    backgroundColor: 'rgba(219, 50, 54, 0.8)'
                }]
                setLastUpdate(new Date(value.Date).toLocaleString())
                setGlobal(value.Global)
                setSortedCountry(finalSorted)
                setCountryName(countryNames)
            });
        } catch (error) {
            console.error(error)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    console.log(countryName['US'])
    return (
        <>
            <Dialog
                open={!isLandscape}
            >
                <DialogContent>
                    <Grid
                        container
                        alignContent={'center'}
                        alignItems={'center'}
                        justify={'center'}
                    >
                        <Grid item>
                            <RotateRight fontSize={'large'} className={'rotateOrientation'} />
                        </Grid>
                        <Grid item xs={12} md={12}>
                            <Typography variant={'subtitle1'} color={'textSecondary'} align={'center'}>Uh, kinda rotate me for better experience?</Typography>
                        </Grid>
                    </Grid>
                </DialogContent>
            </Dialog>
            {isLandscape ? <Grid container>
                <Grid item xs={12} md={2}>
                    <Grid
                        container
                        justify={'center'}
                        alignContent={'center'}
                        alignItems={'center'}
                        style={{ height: 'calc(100vh - 48px)' }}
                    >
                        <Grid item xs={3} md={12}>
                            <List
                                subheader={
                                    <ListSubheader component="div" id="nested-list-subheader">
                                        New Cases (Global)
                            </ListSubheader>
                                }
                            >
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgb(244, 194, 13, 0.8)' }}>
                                            <ErrorOutline />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Confirmed" secondary={global && global.NewConfirmed ? global.NewConfirmed.toLocaleString() : 0} />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgba(219, 50, 54, 0.8)' }}>
                                            <RemoveCircleOutline />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Death" secondary={global && global.NewDeaths ? global.NewDeaths.toLocaleString() : 0} />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgba(60, 186, 84, 0.8)' }}>
                                            <FavoriteBorder />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Recovered" secondary={global && global.NewRecovered ? global.NewRecovered.toLocaleString() : 0} />
                                </ListItem>
                            </List>
                        </Grid>
                        <Grid item xs={3} md={12}>
                            <List
                                subheader={
                                    <ListSubheader component="div" id="nested-list-subheader">
                                        Total Cases (Global)
                            </ListSubheader>
                                }
                            >
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgb(244, 194, 13, 0.8)' }}>
                                            <Error />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Confirmed" secondary={global && global.TotalConfirmed ? global.TotalConfirmed.toLocaleString() : 0} />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgba(219, 50, 54, 0.8)' }}>
                                            <RemoveCircle />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Death" secondary={global && global.TotalDeaths ? global.TotalDeaths.toLocaleString() : 0} />
                                </ListItem>
                                <ListItem>
                                    <ListItemAvatar>
                                        <Avatar style={{ backgroundColor: 'rgba(60, 186, 84, 0.8)' }}>
                                            <Favorite />
                                        </Avatar>
                                    </ListItemAvatar>
                                    <ListItemText primary="Recovered" secondary={global && global.TotalRecovered ? global.TotalRecovered.toLocaleString() : 0} />
                                </ListItem>
                            </List>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item xs={12} md={10} style={{ padding: 15, height: 'calc(100vh - 48px)' }}>
                    <Typography variant={'h6'} align={'center'} style={{ width: '100%' }}>Top 20 Countries</Typography>
                    <Bar
                        data={{
                            datasets: sortedCountry[1],
                            labels: sortedCountry[0]
                        }}
                        options={{
                            maintainAspectRatio: true,
                            responsive: true,
                            title: {
                                display: true,
                                text: 'Last update at ' + lastUpdate + '. Source: https://api.covid19api.com/',
                                position: 'bottom'
                            },
                            tooltips: {
                                callbacks: {
                                    label: (tooltipItem, data) => {
                                        return tooltipItem.yLabel.toLocaleString();
                                    },
                                    title: (tooltipItem, data) => {
                                        console.log(tooltipItem)
                                        return countryName[tooltipItem[0].label];
                                    }
                                }
                            }
                        }}
                    />
                </Grid>
            </Grid> : null}
        </>
    )
}

export default Covid