import { Grid, LinearProgress, Typography } from '@material-ui/core';
import React, { Component } from 'react';

export default class BlogContainer extends Component {
    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            title: '',
            content: ''
        };

        this.handleNetworkErrors = this.handleNetworkErrors.bind(this);
    }

    componentDidMount() {
        this.getGist();
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (prevProps.gist !== this.props.gist) this.getGist();
    }

    async getGist() {
        const { gist, file } = this.props,
            id = gist.split('/')[1];

        if (!id) return this.setState({ loading: false, error: `${gist} is not valid format` });

        await this.setState({ loading: true });
        this.setupCallback(id);

        const script = document.createElement('script');
        let url = `https://gist.github.com/${gist}.json?callback=gist_callback_${id}`;
        if (file) url += `&file=${file}`;
        script.type = 'text/javascript';
        script.src = url;
        script.onerror = (e) => this.handleNetworkErrors(e);
        document.head.appendChild(script);
    }

    handleNetworkErrors(e) {
        this.setState({
            loading: false,
            error: `${this.props.gist} failed to load`
        });
    }

    setupCallback(id) {
        window[`gist_callback_${id}`] = function (gist) {
            const nextState = { loading: false, error: gist.error || null };
            if (!nextState.error) {
                nextState.title = gist.description;
                nextState.content = `${gist.div.replace(/href=/g, 'target="_blank" href=')}`
            }
            this.setState(nextState);
        }.bind(this);
    }

    render() {
        if (this.state.loading) {
            return <Grid
                container
                style={{ width: '100%', padding: 10, height: '100%' }}
                justify={'center'}
                alignContent={'center'}
                alignItems={'center'}
            >
                <Grid item style={{ width: 250 }}>
                    <LinearProgress variant={'buffer'} value={null} valueBuffer={null} />
                </Grid>
            </Grid>
        } else if (this.state.error) {
            return <Grid
                container
                style={{ width: '100%', padding: 10, height: '100%' }}
                justify={'center'}
                alignContent={'center'}
                alignItems={'center'}
            >
                <Grid item style={{ width: 400 }}>
                    <Typography variant={'h6'} align={'center'} color={'textSecondary'}>This may tak longer than expected...</Typography>
                    <LinearProgress variant={'buffer'} value={null} valueBuffer={null} />
                </Grid>
            </Grid>
        } else {
            const removeElements = (elms) => elms.forEach(el => el.remove());
            var parser = new DOMParser();
            var doc = parser.parseFromString(this.state.content, 'text/html')
            removeElements(doc.querySelectorAll(".gist-meta"));
            var finalHTML = doc.body.outerHTML.replace('<body>', '').replace('</body>', '').trim();
            return <section className={'gist__content'} dangerouslySetInnerHTML={{ __html: finalHTML }} />
        }
    }
}