import { Button, Card, CardActions, CardContent, Chip, Dialog, DialogActions, DialogContent, DialogTitle, Divider, Grid, LinearProgress, makeStyles, Tooltip, Typography } from '@material-ui/core';
import { Link as RouterLink } from "react-router-dom";
import { Schedule } from '@material-ui/icons';
import BlogContainer from './BlogContainer'
import React from 'react';
import Skeleton from '@material-ui/lab/Skeleton';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'center',
        flexWrap: 'wrap',
        '& > *': {
            margin: theme.spacing(0.5),
        },
    },
}));

const Blogs = (props) => {
    const setPageDescription = props.setPageDescription
    const setPageTitle = props.setPageTitle
    const uniqueId = props.openId
    const setRefresher = props.setRefresher
    const classes = useStyles()
    const setTitle = props.setTitle
    const [blogs, setBlogs] = React.useState([])
    const [blogId, setBlogId] = React.useState()
    const [openBlog, setOpenBlog] = React.useState(false)
    const ButtonLink = (props) => {
        const { title, description, to } = props;
        return (
            <Button color={'primary'} onClick={() => {
                setRefresher(Date.now())
                setOpenBlog(true)
                setPageTitle(title)
                setPageDescription(description)
            }} component={React.useMemo(
                () => React.forwardRef((itemProps, ref) => <RouterLink to={to} ref={ref} {...itemProps} />),
                [to],
            )} variant={'contained'} fullWidth>Read Blog</Button>
        )
    }
    React.useEffect(() => {
        if (uniqueId) {
            setBlogId(uniqueId)
            setOpenBlog(true)
        } else {
            setBlogId(undefined)
        }
    }, [uniqueId])
    React.useEffect(() => {
        setTitle('Blogs')
        try {
            fetch('https://api.github.com/users/azharizkita/gists').then((response) =>
                response.json()
            ).then((result) => {
                var processed = {}
                result.forEach(blog => {
                    var metadata = blog.description.split('-')
                    var type = metadata[0].trim()
                    if (type === 'Blog') {
                        processed[+new Date(blog.created_at)] = blog
                        processed[+new Date(blog.created_at)]['type'] = type
                        processed[+new Date(blog.created_at)]['title'] = metadata[1].trim()
                        processed[+new Date(blog.created_at)]['description'] = metadata[2].trim()
                    }
                });
                var sorted = []
                Object.keys(processed).sort((a, b) => b - a).forEach((value) => sorted.push(processed[value]))
                var resorted = {}
                sorted.forEach((sortedBlog) => {
                    resorted[sortedBlog.id] = sortedBlog
                })
                setBlogs(resorted)
            })
        } catch (error) {
            console.error(error)
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])
    return (
        <>
            <Dialog
                fullScreen
                open={openBlog}
                onClose={() => {
                    setPageTitle('')
                    setPageDescription('')
                    setOpenBlog(false)
                }}
            >
                <DialogTitle className={classes.root}>{blogs && blogs[blogId] ? blogs[blogId].title : <Skeleton width={250} height={35} />}</DialogTitle>
                <DialogContent>
                    <BlogContainer
                        gist={'azharizkita/' + blogId}
                        file={'index.md'}
                    />
                </DialogContent>
                <DialogActions>
                    <Button variant={'contained'} onClick={() => {
                        setPageTitle('')
                        setPageDescription('')
                        setOpenBlog(false)
                    }} color={'primary'}>Close</Button>
                </DialogActions>
            </Dialog>
            <Grid
                container
                style={{ width: '100%', padding: 10, height: '85vh' }}
                justify={'center'}
                alignContent={'center'}
                alignItems={'center'}
            >
                {Object.keys(blogs).length !== 0 ? (
                    Object.values(blogs).map((blog, i) => (
                        <Grid key={i} item xs={12} md={4} lg={4} style={{ padding: 15 }}>
                            <Card variant={'outlined'}>
                                <CardContent style={{ minHeight: 220 }}>
                                    <Typography variant={'h6'} align={'center'} color={'textPrimary'}>
                                        {blog.title}
                                    </Typography>
                                    <div style={{ paddingBottom: 10 }} />
                                    <Divider />
                                    <div style={{ paddingBottom: 10 }} />
                                    <Grid
                                        container
                                        className={classes.root}
                                        style={{ width: '100%' }}
                                    >
                                        <Grid item>
                                            <Tooltip title={'Last update'}>
                                                <Chip
                                                    icon={<Schedule />}
                                                    label={new Date(blog.created_at).toLocaleDateString() + ', ' + new Date(blog.created_at).toLocaleTimeString()}
                                                    variant={'outlined'}
                                                />
                                            </Tooltip>
                                        </Grid>
                                    </Grid>
                                    <div style={{ paddingBottom: 10 }} />
                                    <Divider />
                                    <div style={{ paddingBottom: 10 }} />
                                    <Typography align={'center'}>
                                        {blog.description ? blog.description : 'No desription.'}
                                    </Typography>
                                </CardContent>
                                <CardActions>
                                    <ButtonLink title={blog.title} description={blog.description} to={'/?section=blogs&id=' + blog.id} />
                                </CardActions>
                            </Card>
                        </Grid>
                    ))
                ) : <Grid item style={{ width: 250 }}>
                        <LinearProgress variant={'buffer'} value={null} valueBuffer={null} />
                    </Grid>}
            </Grid>
        </>
    )
}

export default Blogs